// app/admin/chat/page.tsx
'use client';

import React, { useState, useRef, useEffect } from 'react';
import { useChat } from '@/hooks/useChat';
import { getCurrentUser } from '@/utils/auth';
import { Button } from '@/app/components/ui/button';
import { Input } from '@/app/components/ui/input';
import { Card, CardContent } from '@/app/components/ui/card';
import { Loader2, Send, User, Bot } from 'lucide-react';
import AuthChecker from '@/app/components/AuthChecker';

export default function ChatPage() {
  return (
    <AuthChecker requireBirthDetails={true}>
      <ChatContent />
    </AuthChecker>
  );
}

function ChatContent() {
  const { messages, loading, error, sendMessage, startConversation } = useChat();
  const [inputValue, setInputValue] = useState('');
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const user = getCurrentUser();

  // Handle form submission
  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (inputValue.trim() && !loading) {
      sendMessage(inputValue);
      setInputValue('');
    }
  };

  // Auto-scroll to bottom of messages
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  return (
    <div className="container mx-auto p-4 max-w-4xl">
      <div className="bg-white rounded-lg shadow-lg overflow-hidden flex flex-col h-[80vh]">
        {/* Chat header */}
        <div className="bg-purple-600 text-white p-4 flex items-center justify-between">
          <h1 className="text-xl font-bold">Parasara Jyotish Consultation</h1>
          <div className="text-sm">
            {user?.name || 'User'}
          </div>
        </div>

        {/* Messages container */}
        <div className="flex-grow overflow-y-auto p-4 space-y-4 bg-purple-50">
          {messages.length === 0 && !loading ? (
            <div className="flex flex-col items-center justify-center h-full text-gray-500">
              <Bot size={48} className="mb-2 text-purple-400" />
              <p>Your astrology consultation will appear here</p>
              <Button 
                variant="outline" 
                className="mt-4"
                onClick={() => startConversation()}
              >
                Start New Consultation
              </Button>
            </div>
          ) : (
            messages.map((message) => (
              <div
                key={message.id}
                className={`flex ${
                  message.role === 'user' ? 'justify-end' : 'justify-start'
                }`}
              >
                <div
                  className={`flex items-start space-x-2 max-w-[80%] ${
                    message.role === 'user'
                      ? 'flex-row-reverse space-x-reverse'
                      : 'flex-row'
                  }`}
                >
                  <div
                    className={`p-2 rounded-full ${
                      message.role === 'user'
                        ? 'bg-purple-600 text-white'
                        : 'bg-gray-200 text-gray-800'
                    }`}
                  >
                    {message.role === 'user' ? (
                      <User size={20} />
                    ) : (
                      <Bot size={20} />
                    )}
                  </div>
                  <Card
                    className={`${
                      message.role === 'user'
                        ? 'bg-purple-600 text-white'
                        : 'bg-white'
                    }`}
                  >
                    <CardContent className="p-3">
                      <p>{message.content}</p>
                      <div 
                        className={`text-xs mt-1 ${
                          message.role === 'user' ? 'text-purple-200' : 'text-gray-400'
                        }`}
                      >
                        {new Date(message.timestamp).toLocaleTimeString()}
                      </div>
                    </CardContent>
                  </Card>
                </div>
              </div>
            ))
          )}

          {/* Loading indicator */}
          {loading && (
            <div className="flex justify-start">
              <Card className="bg-white">
                <CardContent className="p-3 flex items-center space-x-2">
                  <Loader2 size={16} className="animate-spin text-purple-600" />
                  <span className="text-gray-600">Thinking...</span>
                </CardContent>
              </Card>
            </div>
          )}

          {/* Error message */}
          {error && (
            <div className="flex justify-center">
              <Card className="bg-red-50 border-red-200">
                <CardContent className="p-3 text-red-600">
                  <p>Error: {error}</p>
                  <Button
                    variant="outline"
                    size="sm"
                    className="mt-2"
                    onClick={() => startConversation()}
                  >
                    Try Again
                  </Button>
                </CardContent>
              </Card>
            </div>
          )}

          <div ref={messagesEndRef} />
        </div>

        {/* Message input */}
        <form onSubmit={handleSubmit} className="p-4 border-t bg-white">
          <div className="flex space-x-2">
            <Input
              value={inputValue}
              onChange={(e) => setInputValue(e.target.value)}
              placeholder="Type your message here..."
              disabled={loading}
              className="flex-grow"
            />
            <Button type="submit" disabled={loading || !inputValue.trim()}>
              {loading ? (
                <Loader2 size={20} className="animate-spin" />
              ) : (
                <Send size={20} />
              )}
            </Button>
          </div>
        </form>
      </div>

      <div className="mt-4 text-center text-sm text-gray-500">
        <p>
          Your consultation is based on Vedic astrological principles. For a more
          detailed analysis, please consult with a professional astrologer.
        </p>
      </div>
    </div>
  );
}